// Types
import { JSX } from "react"
import { CqsItem } from "@rp/common/api-types"
import { Watchlist } from "@rp/common/api-types"
import { Box } from "@rp/react/web/mui/core"
import { FilterDisplay } from "../FilterDisplay"
import { CategoryWidgetActions, WidgetCategory } from "../hooks/useWidgetCategories"
import { GroupContainer } from "./Group.styles"
// Components
import { GroupTitle } from "./GroupTitle"
import { ShowMoreFamilies } from "./ShowMoreFamilies"

export type GenericGroupProps = {
  widgetCategory: WidgetCategory
  children?: JSX.Element
  onChange?: (item: CqsItem) => void
  onClick?: (item: CqsItem) => void
  onExclude?: (item: CqsItem) => void
  widgetActions: CategoryWidgetActions
  disableCheckbox?: boolean
  disableWatchlist?: boolean
  onAddToWatchlist?: (watchlist: Watchlist) => void
}

const GenericGroup = ({
  widgetCategory,
  onChange,
  onClick,
  onExclude,
  widgetActions,
  disableCheckbox,
  disableWatchlist,
  onAddToWatchlist,
}: GenericGroupProps) => {
  const groupByFamilies = widgetCategory.groups.length > 1 && widgetCategory.groupBy

  if (!widgetCategory.filters?.length) return null

  return (
    <Box>
      <GroupTitle
        title={widgetCategory.label}
        icon={widgetCategory.icon}
        groups={widgetCategory.groups}
        groupBy={widgetCategory.groupBy}
        onGroupByChange={widgetActions.setGroupBy}
        groupId={widgetCategory.id}
      />
      <GroupContainer>
        {groupByFamilies ? (
          <ShowMoreFamilies
            widgetCategory={widgetCategory}
            onChange={onChange}
            onClick={onClick}
            widgetActions={widgetActions}
            disableCheckbox={disableCheckbox}
            disableWatchlist={disableWatchlist}
            onAddToWatchlist={onAddToWatchlist}
            onExclude={onExclude}
          />
        ) : (
          <FilterDisplay
            showMore={widgetCategory.showMore.find(item => item === "") !== undefined}
            toggleShowMore={() => widgetActions.toggleShowMore(widgetCategory.id)}
            filters={widgetCategory.filters}
            onChange={onChange}
            onClick={onClick}
            disableCheckbox={disableCheckbox}
            disableWatchlist={disableWatchlist}
            onAddToWatchlist={onAddToWatchlist}
            onExclude={onExclude}
          />
        )}
      </GroupContainer>
    </Box>
  )
}

export { GenericGroup }
